.crousel{
    display: flex;
    flex-direction: row;
}
.container{
    width: 50%;
    background-color: aqua;
}
/* For screens smaller than 600px */
@media (max-width: 600px) {
    .carousel {
      height: 200px;
      width: 80%;
      margin: 0 auto;
    }
  }
  
  /* For screens larger than 600px */
  @media (min-width: 601px) {
    .carousel {
      height: 300px;
      width: 60%;
      margin: 0 auto;
    }
  }
  .shadow{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;        /* border: 1px solid red; */
    height: 100%;
        }
  